import React, { useState } from "react";
import { FormSubmitHandler, useForm } from "react-hook-form";
import { AddMachineFormType } from "../types";
import { addMachineSchema } from "../schema";
import { yupResolver } from "@hookform/resolvers/yup";
import { useMutation } from "@tanstack/react-query";
import { addMachineApi } from "@/api/machineManagement";

export default function useMachineManagement() {
  const {
    control,
    handleSubmit,
    setValue,
    watch,
    formState: { errors, isValid },
  } = useForm<AddMachineFormType>({
    resolver: yupResolver(addMachineSchema),
    defaultValues: {
      title: "",
      description: "",
      price: "",
      dailyIncome: "",
      fee: "",
      rentalDays: "",
    },
  });
  const handleImageSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setValue("image", file);
    }
  };
  const {
    mutateAsync: addMachine,
    isSuccess,
    isError,
    error,
  } = useMutation({
    mutationFn: addMachineApi.mutationFn,
  });
  const [message, setMessage] = useState("");
  const [openToast, setOpenToast] = useState(false);
  const handleAddMachine: FormSubmitHandler<AddMachineFormType> = async (
    data
  ) => {
    if (!selectedImage) {
      setMessage("Please uplaod an Image");
      return;
    }
    try {
      const { title, description, price, dailyIncome, fee, rentalDays } = data;

      const formData = new FormData();
      formData.append("title", title);
      formData.append("description", description);
      formData.append("price", price);
      formData.append("dailyIncome", dailyIncome);
      formData.append("fee", fee);
      formData.append("rentalDays", rentalDays);

      if (selectedImage) {
        formData.append("image", selectedImage);
      }
      if(isSuccess){
        setOpenToast(true)
        setMessage("Product Added Successfully")
      }
      if(isError){
        setOpenToast(true)
        setMessage(error.message)
      }
      const response = await addMachine(formData);
      console.log(response);
    } catch (err:any) {
      setOpenToast(true)
      if(isError){
        setMessage(error.message)
      }
      setOpenToast(true)
      setMessage(err.data.message)
    }
  };

  const selectedImage = watch("image");
  return {
    control,
    errors,
    handleSubmit,
    selectedImage,
    handleImageSelect,
    handleAddMachine,
    isValid,
    message,
    setOpenToast,
    openToast
  };
}
